<template>
    <div class="chart-container">
        <apexchart class="chart"  type="pie" :options="chartOptions" :series="series"></apexchart>
    </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
    components: {
        apexchart: VueApexCharts
    },
    props: {
        series: Array,
        labels2: Array
    },
    data: () => ({
          /* series: [60, 40], */
           chartOptions: {
            chart: {
              width: 300,
              type: 'pie',
            },
            legend: {
                position: 'bottom',
                fontSize: '14px',
                fontFamily: 'Helvetica, Arial',
                horizontalAlign: 'center', 
                onItemClick: {
                    toggleDataSeries: true
                },
            },
            tooltip: {
                style: {
                    fontSize: '14px',
                    fontFamily: undefined
                },
                custom: function({ series, seriesIndex, w }) {
                return (
                    '<div class="arrow_box">' +
                    "<span>" +
                    w.config.labels[seriesIndex] +': '+ series[seriesIndex]+'%' +
                    "</span>" +
                    "</div>"
                );
                }
            },
            colors: ['#07B2CC','#B81F33'],
            dataLabels: {
              enabled: true,
              formatter: function(val) {
                return val + '%';
              }
            },
            labels: ['Asistencia', 'Inasistencia'],
            responsive: [{
              breakpoint: 480,
              options: {
                chart: {
                  width: 230
                },
                legend: {
                  position: 'bottom',
                  horizontalAlign: 'center', 
                }
              }
            }]
          },
    }),
    created(){
        /* this.chartOptions.labels = this.asistencia.labels; */
    },

}
</script>

<style>

.chart-container {
    width: auto;
    margin: 0;
    padding: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
}
.chart {
    width: 300px;
}
.arrow_box {
  position: relative;
  background: #555;
  border: 2px solid #000000;
}
.arrow_box:after, .arrow_box:before {
  right: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.arrow_box:after {
  border-color: rgba(85, 85, 85, 0);
  border-right-color: #555;
  border-width: 10px;
  margin-top: -10px;
}
.arrow_box:before {
  border-color: rgba(0, 0, 0, 0);
  border-right-color: #000000;
  border-width: 13px;
  margin-top: -13px;
}
.chart .apexcharts-tooltip {
  color: #fff;
  transform: translateX(10px) translateY(10px);
  overflow: visible !important;
  white-space: normal !important;
}

.chart .apexcharts-tooltip span {
  padding: 5px 10px;
  display: inline-block;
}

@media screen and (max-width: 1300px){
    .chart {
        width: 230px;
        height: 200px;
    }
}
</style>